import "./styles.css";
import menu from "./menu";

import * as React from "react";

import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

export default function ZEON1() {
  const [age, setAge] = React.useState("");

  function menuuu(index) {
    const items = [];
    const keys = Object.keys(menu[index]);
    const values = Object.values(menu[index]);
    for (let i = 0; i < values.length; i++) {
      if (i == 0) {
        items.push(<img src={values[i]} alt="s" />);
      } else {
        items.push(<div>{values[i]}</div>);
      }
    }
    return <ul>{items}</ul>;
  }

  return (
    <>
      <div>
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>ジオニック社</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(14)}</div>
                <div className="Gun">{menuuu(15)}</div>
                <div className="Gun">{menuuu(16)}</div>
                <div className="Gun">{menuuu(17)}</div>
                <div className="Gun">{menuuu(18)}</div>
                <div className="Gun">{menuuu(19)}</div>
                <div className="Gun">{menuuu(20)}</div>
                <div className="Gun">{menuuu(21)}</div>
                <div className="Gun">{menuuu(22)}</div>
                <div className="Gun">{menuuu(23)}</div>
                <div className="Gun">{menuuu(24)}</div>
                <div className="Gun">{menuuu(25)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
    </>
  );
}
