import "./styles.css";
import menu from "./menu";

import * as React from "react";

import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

export default function RX1() {
  const [age, setAge] = React.useState("");

  function menuuu(index) {
    const items = [];
    const keys = Object.keys(menu[index]);
    const values = Object.values(menu[index]);
    for (let i = 0; i < values.length; i++) {
      if (i == 0) {
        items.push(<img src={values[i]} alt="s" />);
      } else {
        items.push(<div>{values[i]}</div>);
      }
    }
    return <ul>{items}</ul>;
  }

  return (
    <>
      <div>
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>RXシリーズ</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="ll">
              <div className="Kaigyo">
                <div className="Gun">{menuuu(0)}</div>
                <div className="Gun">{menuuu(1)}</div>
                <div className="Gun">{menuuu(2)}</div>
                <div className="Gun">{menuuu(3)}</div>
                <div className="Gun">{menuuu(4)}</div>
                <div className="Gun">{menuuu(5)}</div>
                <div className="Gun">{menuuu(6)}</div>
                <div className="Gun">{menuuu(7)}</div>
                <div className="Gun">{menuuu(8)}</div>
                <div className="Gun">{menuuu(9)}</div>
                <div className="Gun">{menuuu(10)}</div>
                <div className="Gun">{menuuu(11)}</div>
                <div className="Gun">{menuuu(12)}</div>
                <div className="Gun">{menuuu(13)}</div>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
    </>
  );
}
